/* RESPONSIVE CSS */
 :root {
    --body-fg: #373838;
    --body-bg: #fff;
    --body-th: #0e24d8;
}

@media (prefers-reduced-motion: no-preference) {
     :root {
        scroll-behavior: smooth;
    }
}

.cursor_pointer {
    cursor: pointer;
}


/* Additional css*/


/* profile__dashboard */

.profile__dashboard .card {
    margin-bottom: 30px;
    border: none;
    border-radius: 5px;
    box-shadow: 0px 0 30px rgba(1, 41, 112, 0.1);
    overflow: hidden;
}

.profile__dashboard .filter {
    position: absolute;
    right: 0px;
    top: 15px;
}

.profile__dashboard .filter .icon {
    color: #aab7cf;
    padding-right: 20px;
    padding-bottom: 5px;
    transition: 0.3s;
    font-size: 40px;
    cursor: pointer;
}

.profile__dashboard .card-body {
    padding: 0 20px 20px 20px;
}

.profile__dashboard .recents .dataTable-top {
    padding: 0 0 10px 0;
}

.profile__dashboard .dataTable-top>nav:first-child,
.profile__dashboard .dataTable-top>div:first-child,
.profile__dashboard .dataTable-bottom>nav:first-child,
.profile__dashboard .dataTable-bottom>div:first-child {
    float: left;
}

.profile__dashboard .dataTable-top>nav:last-child,
.profile__dashboard .dataTable-top>div:last-child,
.profile__dashboard .dataTable-bottom>nav:last-child,
.profile__dashboard .dataTable-bottom>div:last-child {
    float: right;
}

.profile__dashboard .dataTable-top::after,
.dataTable-bottom::after {
    clear: both;
    content: " ";
    display: table;
}

.profile__dashboard .dataTable-wrapper .dataTable-container {
    border-bottom: 1px solid #d9d9d9;
}

.profile__dashboard .dataTable-wrapper .dataTable-container .dataTable-table {
    max-width: 100%;
    width: 100%;
    border-spacing: 0;
    border-collapse: separate;
}

.profile__dashboard .recents .table thead {
    background: #f6f6fe;
}

.profile__dashboard .card-title {
    padding: 20px 0 15px 0;
    font-size: 18px;
    font-weight: 500;
    color: #012970;
    font-family: "Poppins", sans-serif;
}

.profile__dashboard .recents {
    font-size: 14px;
}

.profile__dashboard .recents .dataTable-bottom {
    padding: 10px 0 0 0;
}


/* No input */

#no-input.contact {
    position: absolute;
    width: 60vw;
    top: 60px;
    right: 10px;
    min-width: 320px;
    overflow: hidden;
    max-width: 500px;
    z-index: 100;
    background-color: white;
    padding: 10px;
}

#no-input.contact form .input-group-text {
    height: 44px;
}


/* BLOCKQUOTE */

.blockquote {
    padding: 60px 80px 40px;
    position: relative;
}

.blockquote p {
    font-family: "Utopia-italic";
    font-size: 35px;
    font-weight: 700px;
    text-align: center;
}

.blockquote:before {
    position: absolute;
    font-family: 'FontAwesome';
    top: 0;
    content: "\F10D";
    font-size: 200px;
    color: rgba(0, 0, 0, 0.1);
}

.blockquote::after {
    content: "";
    top: 10px;
    left: 50%;
    margin-left: -100px;
    position: absolute;
    border-bottom: 3px solid red;
    height: 3px;
    width: 200px;
}
.blockquote {
    margin: 50px auto;
    font-style: italic;
    color: #555555;
    padding: 2em 30px 1.2em 75px;
    border-left: 8px solid #78C0A8;
    line-height: 1.6;
    position: relative;
    /* background: #EDEDED; */
    font-size: 1rem;
}

.blockquote::before {
    font-family: Arial;
    content: "\201C";
    color: #78C0A8;
    font-size: 4em;
    position: absolute;
    left: 10px;
    top: -10px;
}

.blockquote::after {
    content: '';
}

.blockquote span {
    display: block;
    color: #333333;
    font-style: normal;
    font-weight: bold;
    margin-top: 1em;
}


/* ERROR PAGE */

#error-404.error-404 {
    padding: 30px;
}

#error-404.error-404 h1 {
    font-size: 180px;
    font-weight: 700;
    color: #4154f1;
    margin-bottom: 0;
    line-height: 150px;
}

#error-404.error-404 h2 {
    font-size: 24px;
    font-weight: 700;
    color: #012970;
    margin-bottom: 30px;
}

#error-404.error-404 .btn {
    background: #51678f;
    color: #fff;
    padding: 8px 30px;
}

@media screen and (min-width:992px) {
    #error-404.error-404 svg {
        max-width: 50%;
        height: auto;
    }
}


/*--------------------------------------------------------------
# General
--------------------------------------------------------------*/

body {
    margin: 0;
    padding: 0;
    font-family: "Roboto", "Lucida Grande", "DejaVu Sans", "Bitstream Vera Sans", Verdana, Arial, sans-serif;
    color: #373838;
    color: var(--body-fg);
    background: #fff;
    background: var(--body-bg);
    font-size: 1.1rem;
    line-height: 1.8;
}

a {
    color: #0e24d8;
    color: var(--body-th);
    text-decoration: none;
}

a:hover {
    color: #067ded;
    text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: "Raleway", sans-serif;
}


/*--------------------------------------------------------------
# Back to top button
--------------------------------------------------------------*/

.back-to-top {
    position: fixed;
    visibility: hidden;
    opacity: 0;
    right: 15px;
    bottom: 15px;
    z-index: 100;
    background: #0e24d8;
    background: var(--body-th);
    width: 40px;
    height: 40px;
    border-radius: 50px;
    transition: all 0.4s;
}

.back-to-top i {
    font-size: 28px;
    color: #fff;
    line-height: 0;
}

.back-to-top:hover {
    background: #0678e3;
    color: #fff;
}

.back-to-top.active {
    visibility: visible;
    opacity: 1;
}


/*--------------------------------------------------------------
# Preloader
--------------------------------------------------------------*/

#preloader {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    overflow: hidden;
    background: #fff;
}

#preloader:before {
    content: "";
    position: fixed;
    top: calc(50% - 30px);
    left: calc(50% - 30px);
    border: 6px solid #0e24d8;
    border: 6px solid var(--body-th);
    border-top-color: #fff;
    border-bottom-color: #fff;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    -webkit-animation: animate-preloader 1s linear infinite;
    animation: animate-preloader 1s linear infinite;
}

@-webkit-keyframes animate-preloader {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes animate-preloader {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}


/*--------------------------------------------------------------
# Disable aos animation delay on mobile devices
--------------------------------------------------------------*/

@media screen and (max-width: 768px) {
    [data-aos-delay] {
        transition-delay: 0 !important;
    }
}


/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/

#header {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 9997;
    transition: all 0.5s;
    padding: 15px;
    overflow-y: auto;
}

@media (max-width: 992px) {
    #header {
        width: 300px;
        background: #fff;
        border-right: 1px solid #e6e9ec;
        left: -300px;
    }
}

@media (min-width: 992px) {
    #main {
        margin-left: 100px;
    }
}


/*--------------------------------------------------------------
# Navigation Menu
--------------------------------------------------------------*/


/**
* Desktop Navigation 
*/

.nav-menu {
    padding: 0;
    display: block;
}

.nav-menu * {
    margin: 0;
    padding: 0;
    list-style: none;
}

.nav-menu>ul>li {
    position: relative;
    white-space: nowrap;
}

.nav-menu a,
.nav-menu a:focus {
    display: flex;
    align-items: center;
    color: #45505b;
    padding: 10px 18px;
    margin-bottom: 8px;
    transition: 0.3s;
    font-size: 15px;
    border-radius: 50px;
    background: #f2f3f5;
    height: 56px;
    width: 100%;
    overflow: hidden;
    transition: 0.3s;
}

.nav-menu a i,
.nav-menu a:focus i {
    font-size: 20px;
}

.nav-menu a span,
.nav-menu a:focus span {
    padding: 0 5px 0 7px;
    color: #45505b;
}

@media (min-width: 992px) {
    .nav-menu a,
    .nav-menu a:focus {
        width: 56px;
    }
    .nav-menu a span,
    .nav-menu a:focus span {
        display: none;
        color: #fff;
    }
}

.nav-menu a:hover,
.nav-menu .active,
.nav-menu .active:focus,
.nav-menu li:hover>a {
    color: #fff;
    background: #0e24d8;
    background: var(--body-th);
}

.nav-menu a:hover span,
.nav-menu .active span,
.nav-menu .active:focus span,
.nav-menu li:hover>a span {
    color: #fff;
}

.nav-menu a:hover,
.nav-menu li:hover>a {
    width: 100%;
    color: #fff;
}

.nav-menu a:hover span,
.nav-menu li:hover>a span {
    display: block;
}


/**
* Mobile Navigation 
*/

.mobile-nav-toggle {
    position: fixed;
    right: 10px;
    top: 10px;
    z-index: 100;
    border: 0;
    background: none;
    font-size: 28px;
    transition: all 0.4s;
    outline: none !important;
    line-height: 0;
    cursor: pointer;
    border-radius: 50px;
    padding: 5px;
    color: #0e24d8;
    color: var(--body-th);
}

.mobile-nav-toggle i {
    color: #45505b;
}

.mobile-nav-active {
    overflow: hidden;
}

.mobile-nav-active #header {
    left: 0;
    box-shadow: 0 1rem 3rem rgba(0, 0, 0, .175) !important;
}

.mobile-nav-active .mobile-nav-toggle {
    color: #fff;
    background-color: #0e24d8;
    background-color: var(--body-th);
}

@media (min-width: 992px) {
    .mobile-nav-toggle {
        display: none;
    }
}


/*--------------------------------------------------------------
# Hero Section
--------------------------------------------------------------*/

#hero {
    width: 100%;
    height: 100vh;
    /* background: url("../img/hero-bg.jpg") top right no-repeat; */
    background-size: cover;
    position: relative;
}

@media (min-width: 992px) {
    #hero {
        padding-left: 160px;
    }
}

#hero h1 {
    margin: 0;
    font-size: 64px;
    font-weight: 700;
    line-height: 56px;
    color: #385777;
}

#hero p {
    color: #45505b;
    margin: 15px 0 0 0;
    font-size: 26px;
    font-family: "Poppins", sans-serif;
}

#hero p span {
